import React, { useState, useEffect } from 'react';
import {
  Grid,
  Card,
  CardContent,
  Typography,
  Box,
  Chip,
  LinearProgress,
  Paper,
} from '@mui/material';
import {
  CheckCircle as SuccessIcon,
  Error as ErrorIcon,
  Schedule as PendingIcon,
  PlayArrow as RunningIcon,
  Timeline as TimelineIcon,
} from '@mui/icons-material';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

interface DashboardMetrics {
  totalPipelines: number;
  successRate: number;
  failureRate: number;
  averageBuildTime: number;
  runningPipelines: number;
  pendingPipelines: number;
}

interface PipelineStatus {
  id: string;
  name: string;
  status: 'success' | 'failure' | 'running' | 'pending';
  repository: string;
  branch: string;
  startedAt: string;
  duration?: number;
}

const Dashboard: React.FC = () => {
  const [metrics, setMetrics] = useState<DashboardMetrics>({
    totalPipelines: 0,
    successRate: 0,
    failureRate: 0,
    averageBuildTime: 0,
    runningPipelines: 0,
    pendingPipelines: 0,
  });

  const [recentPipelines, setRecentPipelines] = useState<PipelineStatus[]>([]);
  const [loading, setLoading] = useState(true);

  // Mock data for demonstration
  useEffect(() => {
    // Simulate API call
    setTimeout(() => {
      setMetrics({
        totalPipelines: 156,
        successRate: 87.2,
        failureRate: 12.8,
        averageBuildTime: 8.5,
        runningPipelines: 3,
        pendingPipelines: 2,
      });

      setRecentPipelines([
        {
          id: '1',
          name: 'main-pipeline',
          status: 'success',
          repository: 'myapp/frontend',
          branch: 'main',
          startedAt: '2024-01-15T10:30:00Z',
          duration: 5.2,
        },
        {
          id: '2',
          name: 'feature-pipeline',
          status: 'running',
          repository: 'myapp/backend',
          branch: 'feature/auth',
          startedAt: '2024-01-15T10:25:00Z',
        },
        {
          id: '3',
          name: 'deploy-pipeline',
          status: 'failure',
          repository: 'myapp/infrastructure',
          branch: 'main',
          startedAt: '2024-01-15T10:20:00Z',
          duration: 12.1,
        },
      ]);

      setLoading(false);
    }, 1000);
  }, []);

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'success':
        return 'success';
      case 'failure':
        return 'error';
      case 'running':
        return 'info';
      case 'pending':
        return 'warning';
      default:
        return 'default';
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'success':
        return <SuccessIcon />;
      case 'failure':
        return <ErrorIcon />;
      case 'running':
        return <RunningIcon />;
      case 'pending':
        return <PendingIcon />;
      default:
        return <TimelineIcon />;
    }
  };

  const formatDuration = (minutes: number) => {
    if (minutes < 60) {
      return `${minutes.toFixed(1)}m`;
    }
    const hours = Math.floor(minutes / 60);
    const remainingMinutes = minutes % 60;
    return `${hours}h ${remainingMinutes.toFixed(0)}m`;
  };

  if (loading) {
    return (
      <Box sx={{ width: '100%' }}>
        <LinearProgress />
      </Box>
    );
  }

  return (
    <Box>
      <Typography variant="h4" gutterBottom>
        Dashboard
      </Typography>

      {/* Metrics Cards */}
      <Grid container spacing={3} sx={{ mb: 4 }}>
        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Total Pipelines
              </Typography>
              <Typography variant="h4" component="div">
                {metrics.totalPipelines}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Success Rate
              </Typography>
              <Typography variant="h4" component="div" color="success.main">
                {metrics.successRate}%
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Average Build Time
              </Typography>
              <Typography variant="h4" component="div">
                {formatDuration(metrics.averageBuildTime)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>

        <Grid item xs={12} sm={6} md={3}>
          <Card>
            <CardContent>
              <Typography color="textSecondary" gutterBottom>
                Active Pipelines
              </Typography>
              <Typography variant="h4" component="div" color="info.main">
                {metrics.runningPipelines + metrics.pendingPipelines}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Charts and Recent Activity */}
      <Grid container spacing={3}>
        {/* Build Trends Chart */}
        <Grid item xs={12} md={8}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Build Trends (Last 7 Days)
              </Typography>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart
                  data={[
                    { date: 'Jan 9', success: 12, failure: 2 },
                    { date: 'Jan 10', success: 15, failure: 1 },
                    { date: 'Jan 11', success: 18, failure: 3 },
                    { date: 'Jan 12', success: 14, failure: 2 },
                    { date: 'Jan 13', success: 16, failure: 1 },
                    { date: 'Jan 14', success: 20, failure: 2 },
                    { date: 'Jan 15', success: 17, failure: 1 },
                  ]}
                >
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="date" />
                  <YAxis />
                  <Tooltip />
                  <Line type="monotone" dataKey="success" stroke="#4caf50" strokeWidth={2} />
                  <Line type="monotone" dataKey="failure" stroke="#f44336" strokeWidth={2} />
                </LineChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </Grid>

        {/* Recent Pipelines */}
        <Grid item xs={12} md={4}>
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Recent Pipelines
              </Typography>
              <Box sx={{ maxHeight: 300, overflow: 'auto' }}>
                {recentPipelines.map((pipeline) => (
                  <Paper
                    key={pipeline.id}
                    sx={{
                      p: 2,
                      mb: 2,
                      borderLeft: `4px solid ${
                        pipeline.status === 'success'
                          ? '#4caf50'
                          : pipeline.status === 'failure'
                          ? '#f44336'
                          : pipeline.status === 'running'
                          ? '#2196f3'
                          : '#ff9800'
                      }`,
                    }}
                  >
                    <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
                      <Typography variant="subtitle2" noWrap>
                        {pipeline.name}
                      </Typography>
                      <Chip
                        icon={getStatusIcon(pipeline.status)}
                        label={pipeline.status}
                        color={getStatusColor(pipeline.status) as any}
                        size="small"
                      />
                    </Box>
                    <Typography variant="body2" color="textSecondary" noWrap>
                      {pipeline.repository}  {pipeline.branch}
                    </Typography>
                    {pipeline.duration && (
                      <Typography variant="body2" color="textSecondary">
                        Duration: {formatDuration(pipeline.duration)}
                      </Typography>
                    )}
                  </Paper>
                ))}
              </Box>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Box>
  );
};

export default Dashboard;
